<template>
  <view class="camera-capture-page">
    <!-- 顶部导航栏 -->
    <view class="camera-header" :style="{ paddingTop: statusBarHeight + 'px' }">
      <view class="header-content">
        <view class="header-left" @tap="goBack">
          <text class="back-icon">←</text>
        </view>
        <view class="header-center">
          <text class="header-title">{{ documentInfo.name }}拍摄</text>
        </view>
        <view class="header-right">
          <view class="settings-icon" @tap="showSettings">
            <text class="icon">⚙</text>
          </view>
        </view>
      </view>
    </view>

    <!-- 相机组件 -->
    <UCameraOverlay
      :devicePosition="cameraDevice"
      :showGuides="showGuides"
      :showControls="true"
      :tips="cameraTips"
      :guideColor="guideColor"
      :crosshairColor="crosshairColor"
      :guideLineWidth="guideLineWidth"
      @capture="onPhotoCapture"
      @update:devicePosition="onDeviceChange"
    />

    <!-- 拍摄引导提示 -->
    <view class="guide-overlay" v-if="showGuideOverlay">
      <view class="guide-content">
        <view class="guide-icon">📸</view>
        <text class="guide-title">拍摄指引</text>
        <text class="guide-text">请将脸部对准中央区域，保持光线充足，面部清晰可见</text>
        <view class="guide-actions">
          <view class="guide-btn" @tap="hideGuideOverlay">我知道了</view>
        </view>
      </view>
    </view>

    <!-- 设置面板 -->
    <view class="settings-panel" v-if="showSettingsPanel" @tap="hideSettings">
      <view class="panel-content" @tap.stop>
        <view class="panel-header">
          <text class="panel-title">拍摄设置</text>
          <view class="close-btn" @tap="hideSettings">
            <text class="close-icon">×</text>
          </view>
        </view>
        
        <view class="setting-item">
          <text class="setting-label">显示引导线</text>
          <view class="setting-switch" :class="{ active: showGuides }" @tap="toggleGuides">
            <view class="switch-dot"></view>
          </view>
        </view>
        
        <view class="setting-item">
          <text class="setting-label">引导线颜色</text>
          <view class="color-picker">
            <view 
              class="color-option" 
              v-for="color in guideColors" 
              :key="color.value"
              :style="{ backgroundColor: color.value }"
              :class="{ active: guideColor === color.value }"
              @tap="selectGuideColor(color.value)"
            ></view>
          </view>
        </view>
        
        <view class="setting-item">
          <text class="setting-label">引导线粗细</text>
          <view class="slider-container">
            <slider 
              :value="guideLineWidth" 
              min="1" 
              max="8" 
              step="1"
              @change="onLineWidthChange"
              activeColor="#6C5CE7"
            />
            <text class="slider-value">{{ guideLineWidth }}px</text>
          </view>
        </view>
      </view>
    </view>

    <!-- 底部工具栏 -->
    <view class="camera-toolbar">
      <view class="toolbar-item" @tap="selectFromAlbum">
        <view class="tool-icon">🖼</view>
        <text class="tool-label">相册</text>
      </view>
      
      <view class="capture-btn" @tap="capturePhoto">
        <view class="capture-inner">
          <view class="capture-dot"></view>
        </view>
      </view>
      
      <view class="toolbar-item" @tap="toggleFlash">
        <view class="tool-icon">{{ flashMode === 'on' ? '💡' : '🔆' }}</view>
        <text class="tool-label">闪光灯</text>
      </view>
    </view>

    <!-- 加载状态 -->
    <view class="loading-overlay" v-if="isProcessing">
      <view class="loading-content">
        <view class="loading-spinner"></view>
        <text class="loading-text">正在处理图片...</text>
      </view>
    </view>
  </view>
</template>

<script>
import UCameraOverlay from '@/components/ucamera-overlay/ucamera-overlay.vue'

export default {
  name: 'CameraCapture',
  components: {
    UCameraOverlay
  },
  data() {
    return {
      statusBarHeight: 0,
      documentInfo: {
        name: '身份证',
        specs: {
          printSize: '26x32mm',
          pixelSize: '358x441px',
          resolution: '300DPI'
        }
      },
      
      // 相机设置
      cameraDevice: 'front',
      showGuides: true,
      cameraTips: '请将脸部对准中央区域',
      guideColor: 'rgba(0,200,255,0.9)',
      crosshairColor: 'rgba(255,255,255,0.6)',
      guideLineWidth: 4,
      flashMode: 'off',
      
      // UI状态
      showGuideOverlay: true,
      showSettingsPanel: false,
      isProcessing: false,
      
      // 设置选项
      guideColors: [
        { name: '蓝色', value: 'rgba(0,200,255,0.9)' },
        { name: '绿色', value: 'rgba(0,255,100,0.9)' },
        { name: '红色', value: 'rgba(255,100,100,0.9)' },
        { name: '紫色', value: 'rgba(200,100,255,0.9)' },
        { name: '橙色', value: 'rgba(255,150,0,0.9)' }
      ]
    }
  },
  
  onLoad(options) {
    // 获取系统信息
    const systemInfo = uni.getSystemInfoSync()
    this.statusBarHeight = systemInfo.statusBarHeight || 0
    
    // 获取传递的文档信息
    if (options.data) {
      try {
        this.documentInfo = JSON.parse(decodeURIComponent(options.data))
        this.cameraTips = `请拍摄${this.documentInfo.name}，对准中央区域`
      } catch (e) {
        console.error('解析文档数据失败:', e)
      }
    }
    
    // 延迟隐藏引导提示
    setTimeout(() => {
      this.showGuideOverlay = false
    }, 3000)
  },
  
  methods: {
    goBack() {
      uni.navigateBack()
    },
    
    showSettings() {
      this.showSettingsPanel = true
    },
    
    hideSettings() {
      this.showSettingsPanel = false
    },
    
    hideGuideOverlay() {
      this.showGuideOverlay = false
    },
    
    toggleGuides() {
      this.showGuides = !this.showGuides
    },
    
    selectGuideColor(color) {
      this.guideColor = color
    },
    
    onLineWidthChange(e) {
      this.guideLineWidth = e.detail.value
    },
    
    onDeviceChange(device) {
      this.cameraDevice = device
    },
    
    toggleFlash() {
      this.flashMode = this.flashMode === 'on' ? 'off' : 'on'
    },
    
    selectFromAlbum() {
      uni.chooseImage({
        count: 1,
        sizeType: ['original'],
        sourceType: ['album'],
        success: (res) => {
          this.processPhoto(res.tempFilePaths[0], 'album')
        },
        fail: () => {
          uni.showToast({
            title: '选择图片失败',
            icon: 'none'
          })
        }
      })
    },
    
    capturePhoto() {
      // 触发相机组件的拍照功能
      // 这里可以通过ref调用UCameraOverlay的拍照方法
      // 或者使用uni.chooseImage作为备选方案
      uni.chooseImage({
        count: 1,
        sizeType: ['original'],
        sourceType: ['camera'],
        success: (res) => {
          this.processPhoto(res.tempFilePaths[0], 'camera')
        },
        fail: () => {
          uni.showToast({
            title: '拍照失败',
            icon: 'none'
          })
        }
      })
    },
    
    onPhotoCapture(imagePath) {
      // UCameraOverlay组件的拍照回调
      this.processPhoto(imagePath, 'ucamera')
    },
    
    processPhoto(imagePath, source) {
      this.isProcessing = true
      
      console.log('处理图片:', imagePath, '来源:', source)
      
      // 模拟图片处理过程
      setTimeout(() => {
        this.isProcessing = false
        
        // 跳转到预览页面或返回上一页
        uni.navigateTo({
          url: `/pages/photo-preview/photo-preview?image=${encodeURIComponent(imagePath)}&document=${encodeURIComponent(JSON.stringify(this.documentInfo))}`
        })
      }, 2000)
    }
  }
}
</script>

<style scoped>
.camera-capture-page {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: #000;
}

/* 顶部导航栏 */
.camera-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: linear-gradient(180deg, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0.4) 70%, transparent 100%);
}

.header-content {
  height: 88rpx;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 32rpx;
}

.header-left,
.header-right {
  width: 88rpx;
  height: 88rpx;
  display: flex;
  align-items: center;
  justify-content: center;
}

.back-icon,
.icon {
  font-size: 48rpx;
  color: #fff;
  font-weight: 300;
}

.header-center {
  flex: 1;
  text-align: center;
}

.header-title {
  font-size: 32rpx;
  color: #fff;
  font-weight: 500;
}

/* 引导提示覆盖层 */
.guide-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 2000;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 64rpx;
}

.guide-content {
  background: #fff;
  border-radius: 32rpx;
  padding: 80rpx 60rpx;
  text-align: center;
  max-width: 600rpx;
}

.guide-icon {
  font-size: 120rpx;
  margin-bottom: 32rpx;
}

.guide-title {
  display: block;
  font-size: 40rpx;
  font-weight: 600;
  color: #333;
  margin-bottom: 24rpx;
}

.guide-text {
  display: block;
  font-size: 28rpx;
  color: #666;
  line-height: 1.6;
  margin-bottom: 48rpx;
}

.guide-actions {
  display: flex;
  justify-content: center;
}

.guide-btn {
  background: #6C5CE7;
  color: #fff;
  padding: 24rpx 48rpx;
  border-radius: 48rpx;
  font-size: 28rpx;
  font-weight: 500;
}

/* 设置面板 */
.settings-panel {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 1500;
  display: flex;
  align-items: flex-end;
}

.panel-content {
  background: #fff;
  border-radius: 32rpx 32rpx 0 0;
  padding: 48rpx 32rpx;
  width: 100%;
  max-height: 80vh;
  overflow-y: auto;
}

.panel-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 48rpx;
  padding-bottom: 24rpx;
  border-bottom: 1rpx solid #F0F0F0;
}

.panel-title {
  font-size: 36rpx;
  font-weight: 600;
  color: #333;
}

.close-btn {
  width: 64rpx;
  height: 64rpx;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #F5F5F5;
  border-radius: 50%;
}

.close-icon {
  font-size: 32rpx;
  color: #666;
}

.setting-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 32rpx 0;
  border-bottom: 1rpx solid #F5F5F5;
}

.setting-item:last-child {
  border-bottom: none;
}

.setting-label {
  font-size: 28rpx;
  color: #333;
}

.setting-switch {
  width: 88rpx;
  height: 48rpx;
  background-color: #E0E0E0;
  border-radius: 24rpx;
  position: relative;
  transition: all 0.3s ease;
}

.setting-switch.active {
  background-color: #6C5CE7;
}

.switch-dot {
  width: 40rpx;
  height: 40rpx;
  background-color: white;
  border-radius: 50%;
  position: absolute;
  top: 4rpx;
  left: 4rpx;
  transition: all 0.3s ease;
  box-shadow: 0 2rpx 8rpx rgba(0, 0, 0, 0.1);
}

.setting-switch.active .switch-dot {
  transform: translateX(40rpx);
}

.color-picker {
  display: flex;
  gap: 16rpx;
}

.color-option {
  width: 48rpx;
  height: 48rpx;
  border-radius: 50%;
  border: 4rpx solid #fff;
  box-shadow: 0 2rpx 8rpx rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.color-option.active {
  border-color: #333;
  transform: scale(1.2);
}

.slider-container {
  display: flex;
  align-items: center;
  gap: 24rpx;
  flex: 1;
  max-width: 300rpx;
}

.slider-value {
  font-size: 24rpx;
  color: #666;
  min-width: 60rpx;
  text-align: right;
}

/* 底部工具栏 */
.camera-toolbar {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(0deg, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0.4) 70%, transparent 100%);
  padding: 48rpx 32rpx 96rpx;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 1000;
}

.toolbar-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12rpx;
  padding: 16rpx;
}

.tool-icon {
  font-size: 48rpx;
  filter: drop-shadow(0 2rpx 4rpx rgba(0, 0, 0, 0.5));
}

.tool-label {
  font-size: 20rpx;
  color: #fff;
  text-shadow: 0 1rpx 2rpx rgba(0, 0, 0, 0.8);
}

.capture-btn {
  width: 160rpx;
  height: 160rpx;
  border-radius: 50%;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 8rpx 32rpx rgba(0, 0, 0, 0.3);
  transition: all 0.2s ease;
}

.capture-btn:active {
  transform: scale(0.95);
}

.capture-inner {
  width: 120rpx;
  height: 120rpx;
  border-radius: 50%;
  border: 8rpx solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #6C5CE7, #A855F7);
}

.capture-dot {
  width: 32rpx;
  height: 32rpx;
  border-radius: 50%;
  background: #fff;
}

/* 加载状态 */
.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 3000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loading-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32rpx;
}

.loading-spinner {
  width: 80rpx;
  height: 80rpx;
  border: 8rpx solid rgba(255, 255, 255, 0.3);
  border-top: 8rpx solid #fff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-text {
  font-size: 28rpx;
  color: #fff;
  text-align: center;
}
</style>
